import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { combineLatest } from 'rxjs';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
 /** Danh sch sn phm hin th trong tab Cho bn */
   list_show_product: any = [];
   /** Danh sch sn phm hin th trong tab Khng cho bn */
   list_show_product_not: any = [];
   list_subproduct: any = [];
   list_product: any = [];
   list_show: any = [];
 
   /** Danh sch sn phm hin th trong tab  n */
   list_show_product_hidden: any = [];
   list_subproduct_hidden: any = [];
   list_product_hidden: any = [];
   list_show_hidden: any = [];
 
   /** Danh sch tt c danh mc */
   list_category: any = [];
   /** Danh sch danh mc tm kim */
   list_search_category: any = [];
   stateObj = {
     /** T kho tm kim */
     keySearch: '',
     /** id ca danh mc c chn */
     selectedValue: '1',
     /** Tab c chn */
     tab: 0,
     nzPageIndex: 1
   }
   observableAll: any;
 
 
 
   constructor(
     private router: Router,
     private vhQueryCafe: VhQueryCafe,
     public vhAlgorithm: VhAlgorithm,
     private languageService: LanguageService,
     private cdRef: ChangeDetectorRef,
     private datePipe: DatePipe,
     private vhComponent: VhComponent,
   ) {
     if (this.router.getCurrentNavigation().extras.state) {
       let stateObj = this.router.getCurrentNavigation().extras.state.stateObj;
       this.stateObj.keySearch = stateObj.keySearch;
       this.stateObj.selectedValue = stateObj.selectedValue;
       this.stateObj.tab = stateObj.tab;
       this.stateObj.nzPageIndex = stateObj.nzPageIndex;
     }
   }
 
 
 
   ngOnInit(): void {
     //x l category
     this.list_category = this.vhQueryCafe.getlocalCategorys(VhType.INGREDIENT);
     if (!this.list_category.some(item => item._id == '1')) {
       this.list_category.unshift({ name: this.languageService.translate('All'), _id: '1' });
     }
     this.list_search_category = this.list_category;
   }
 
 
 
   ngAfterViewInit() {
     this.doRefreshData();
     if (this.stateObj.tab === 2) {
       this.getHiddenProducts();
     }
   }
 
 
 
   ngOnDestroy() {
     this.observableAll.unsubscribe();
   }
 
 
 
   heightScroll: string;
   ngAfterViewChecked() {
     if (document.querySelector("#product-list") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header") && document.querySelector(".ant-table-pagination")) {
       this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
         - document.querySelector(".product-list-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 80 + "px";
     }
     this.cdRef.detectChanges();
   }
 
 
 
   /**get data product, category */
   doRefreshData() {
     this.observableAll = combineLatest([
       this.vhQueryCafe.getObservableLocalProducts(VhType.INGREDIENT)
     ]).subscribe((result: any) => {
       console.log('getObservableLocalProducts', result);
       this.list_product = result[0].filter(item => {
         // check subs
         if (item.subs && item.subs.length) {
           item.subs = item.subs.filter(sub => !sub.delete_hidden)
           return item.subs.some(sub => !sub.delete_hidden);
         }
         else {
           return !item.delete_hidden;
         }
       }).map(item => this.processProduct(item));
       this.list_subproduct = this.vhAlgorithm.sortStringbyASC(this.list_product, 'name');
       if (this.stateObj.keySearch) {
         this.searchProducts(this.stateObj.keySearch);
       }
       else {
         this.searchByCategory(this.stateObj.selectedValue);
       }
     })
   }
 
 
 
   /**
    * X l d liu ca sn phm (units, subproduct, name_category, ...)
    * @param item 
    * @returns 
    */
   processProduct(item: any): any {
     let arr: any = []  // sp xp subproduct theo tin
     if (item.manysize && item.subs.length) {
       arr = this.vhAlgorithm.sortNumberbyASC(item.subs, 'price');
       item['show_expand'] = false;
       item.subs.forEach(element => {
         if (element.units) {
           element['unit_exchange'] = element.units.find(e => e.default ? e.default : e.ratio == 1);
         }
       });
     }
     else {
       if (item.units) {
         item['unit_exchange'] = item.units.find(e => e.default ? e.default : e.ratio == 1);
         item['price_high'] = item.unit_exchange.price;
       }
     }
     return {
       ...item,
       name_category: this.vhQueryCafe.getlocalCategory(item.id_category) ? this.vhQueryCafe.getlocalCategory(item.id_category).name : ''
     }
   }
 
 
 
   /**
    * n / hin sn phm con
    * @example this.renderSubProducts(product_selected)
    */
   renderSubProducts(data) {
     data.show_expand = !data.show_expand
   }
 
 
 
   /**
    * Khi thay i tab
    * @example this.changeTab('1');
    */
   changeTab(value) {
     this.stateObj.tab = value;
     if (value == '1') {
       this.getHiddenProducts();
     }
   }
 
 
   /**
    * cp nht sp theo trng truyn vo
    * @example this.updateProductByField({name: ''})
    */
   updateProductByField(id, value, parent?, is_sub?) {
     console.log('value', value);
     console.log('id', id);
     if (is_sub) {
       this.vhQueryCafe.updateSubProduct_Cafe(id, value).then((res) => {
         console.log(res);
         this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Update successfully"), 2000);
         // kim tra nu tt c con u false th xa khi mng
         if(parent.subs.filter(e => e.delete_hidden).length == 0) {
           this.list_show_product_hidden = this.list_show_product_hidden.filter(item => item._id != parent._id); 
         }
       }, error => {
         console.log(error);
       }).catch(e => {
         console.log(e);
       })
     }
     else {
       this.vhQueryCafe.updateProduct(id, value).then((res) => {
         console.log(res);
         this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Update successfully"), 2000);
         if (!value.delete_hidden) {
           this.list_product_hidden = this.list_product_hidden.filter(item => item._id != id);
           this.list_show_product_hidden = this.list_show_product_hidden.filter(item => item._id != id);
         }
       }, error => {
         console.log(error);
       }).catch(e => {
         console.log(e);
       })
     }
 
   }
 
 
   /**
    * Ly danh sch sn phm  n
    */
   getHiddenProducts() {
     this.vhQueryCafe.refreshLocalProducts_Delete_Hidden(VhType.INGREDIENT).then(() => {
       const hiddenProducts = this.vhQueryCafe.getlocalProducts(VhType.INGREDIENT).filter(item => {
         // check subs
         if (item.subs && item.subs.length) {
           return item.subs.some(sub => sub.delete_hidden);
         }
         else {
           return item.delete_hidden;
         }
       });
 
       this.list_product_hidden = hiddenProducts.map(item => this.processProduct(item));
       this.list_subproduct_hidden = this.vhAlgorithm.sortStringbyASC(this.list_product_hidden, 'name');
       if (this.stateObj.keySearch) {
         this.searchProductsHidden(this.stateObj.keySearch);
       }
       else {
         this.searchByCategoryHidden(this.stateObj.selectedValue);
       }
       
     });
   }
 
 
 
   /** 
    * Lc danh sch sn phm theo delete_hidden
    */
   filterByAllowSell() {
     this.list_show_product = this.list_show.filter(item => item.delete_hidden == false || item.manysize);
     // this.list_show_product_not = this.list_show.filter(item => !item.delete_hidden == false);
   }
 
 
 
   /**
    * Tm kim sn phm theo tn v barcode
    * @param value gi tr tm kim t input
    */
   searchProducts(value) {
     this.stateObj.keySearch = value;
     this.list_product = this.applySearchFilter(this.list_subproduct, this.stateObj.keySearch, ['name', 'barcode', 'item_code']);
     this.searchByCategory(this.stateObj.selectedValue);
   }
 
 
 
   /**
    * Tm kim sn phm (tab  n) theo tn v barcode
    * @param value gi tr tm kim t input
    */
   searchProductsHidden(value) {
     this.stateObj.keySearch = value;
     this.list_product_hidden = this.applySearchFilter(this.list_subproduct_hidden, this.stateObj.keySearch, ['name', 'barcode', 'item_code']);
     this.searchByCategoryHidden(this.stateObj.selectedValue);
   }
 
 
 
   /**
    * Tm kim sn phm theo tn v barcode, nu khng c gi tr tm kim th tr v tt c, ngc li th lc theo tn v barcode
    * @param products 
    * @param searchValue 
    * @param fields 
    * @returns 
    */
   applySearchFilter(products: any[], searchValue: string, fields: string[]): any[] {
     return searchValue
       ? this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(searchValue.toLowerCase()), products, fields)
       : products;
   }
 
 
 
   /** 
    * Lc sn phm theo danh mc sn phm
    */
   searchByCategory(value) {
     this.stateObj.selectedValue = value;
     this.list_show = this.applyCategoryFilter(this.list_product, value);
     this.filterByAllowSell();
   }
 
 
 
   /** 
    * Lc sn phm (tab  n) theo danh mc sn phm
    */
   searchByCategoryHidden(value) {
     this.stateObj.selectedValue = value;
     this.list_show_hidden = this.applyCategoryFilter(this.list_product_hidden, value);
     this.list_show_product_hidden = this.list_show_hidden;
   }
 
 
 
   /**
    * Lc danh sch sn phm theo danh mc, nu danh mc l '1' th tr v tt c sn phm, nu khng th lc theo id_category truyn vo
    * @param products 
    * @param categoryId 
    * @returns 
    */
   applyCategoryFilter(products: any[], categoryId: string): any[] {
     return categoryId === '1'
       ? products
       : products.filter(item => item.id_category === categoryId);
   }
 
 
 
   /**
    * Lc danh mc theo tn
    * @param value 
    */
   search(value) {
     let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
     if (value.length) {
       this.list_category = this.vhAlgorithm.searchList(tempVal, this.list_search_category, ['name']);
     }
     else {
       this.list_category = this.list_search_category;
     }
   }
 
 
 
   gotoAddProducts() {
     this.router.navigate(['/cafe/dashboard/management/ingredient/list/add'], {
       state: {
         stateObj: this.stateObj
       }
     });
   }
 
 
   gotoProductDetail(data, isFromHidden?: boolean) {
     const state = {
       formProduct: data,
       stateObj: this.stateObj
     }
     if (isFromHidden) {
       state['isFromHidden'] = true;
     }
     this.router.navigate(['/cafe/dashboard/management/ingredient/list/edit'], {
       state: state
     });
   }
 
 
   gotoCreateDataProducts() {
     this.router.navigate(['/cafe/dashboard/management/ingredient/list/create-data']);
   }
 
 
   goBack() {
     this.router.navigate(['/cafe/dashboard/management/ingredient']);
   }
 
 
 
   /**
    * xut danh sch sn phm ra file excel v ti v
    * @example this.exportListProduct()
    */
   exportListProduct() {
     let productExcel: Array<any> = [];
     this.list_product.forEach((product, index) => {
       const category: any = this.vhQueryCafe.getlocalCategory(product['id_category'])
       if (!product.manysize) {
         product.units.forEach(unit => {
           let row: Object = new Object()
           row[this.languageService.translate("_name")] = product['name']
           row[this.languageService.translate("_item_code")] = product['item_code']
           row[this.languageService.translate("_category")] = category ? category['name'] : ''
           row[this.languageService.translate("_unit")] = unit['unit']
           row[this.languageService.translate("_brand")] = product['brand']
           row[this.languageService.translate("_price_import")] = unit['price_import']
           row[this.languageService.translate("_price")] = unit['price']
           row[this.languageService.translate("_price2")] = unit['price2']
           row[this.languageService.translate("_barcode")] = unit['barcode']
           row[this.languageService.translate("_warning_number")] = product['warning_number']
           if (row) productExcel.push(row)
         });
       } else {
         product.subs.forEach(subProduct => {
           subProduct.units.forEach(unitsubProduct => {
             let row: Object = new Object()
             row[this.languageService.translate("_name")] = product['name'] + ` (` + subProduct['name'] + `)`
            row[this.languageService.translate("_item_code")] = subProduct['item_code']
             row[this.languageService.translate("_category")] = category ? category['name'] : ''
             row[this.languageService.translate("_unit")] = unitsubProduct['unit']
             row[this.languageService.translate("_brand")] = product['brand']
             row[this.languageService.translate("_price_import")] = unitsubProduct['price_import']
             row[this.languageService.translate("_price")] = unitsubProduct['price']
             row[this.languageService.translate("_price2")] = unitsubProduct['price2']
             row[this.languageService.translate("_barcode")] = unitsubProduct['barcode']
             row[this.languageService.translate("_warning_number")] = subProduct['warning_number']
             if (row) productExcel.push(row)
           });
         });
       }
       if (index == this.list_product.length - 1) {
         this.vhAlgorithm.exportXLSX(productExcel, this.languageService.translate("Product list"))
       }
     })
   }
 
 
 
   /**
    * xut danh sch sn phm ra file excel v ti v
    * @example this.exportListProduct()
    */
   exportListNotSubProduct() {
     let productExcel: Array<any> = [];
     this.list_product.forEach((product, index) => {
       const category: any = this.vhQueryCafe.getlocalCategory(product['id_category'])
       if (!product.manysize) {
         product.units.forEach(unit => {
           let row: Object = new Object()
           row[this.languageService.translate("_name")] = product['name']
           row[this.languageService.translate("_category")] = category ? category['name'] : ''
           row[this.languageService.translate("_unit")] = unit['unit']
           row[this.languageService.translate("_brand")] = product['brand']
           row[this.languageService.translate("_price_import")] = unit['price_import']
           row[this.languageService.translate("_price")] = unit['price']
           row[this.languageService.translate("_price2")] = unit['price2']
           row[this.languageService.translate("_barcode")] = unit['barcode']
           row[this.languageService.translate("_warning_number")] = product['warning_number']
           if (row) productExcel.push(row)
         });
       }
       if (index == this.list_product.length - 1) {
         this.vhAlgorithm.exportXLSX(productExcel, this.languageService.translate("Product list have not manysize"))
       }
     })
   }
 
 
 
   /**
   * xut danh sch sn phm c sub ra file excel v ti v
   * @example this.exportListSubProduct()
   */
   exportListSubProduct() {
     let productExcel: Array<any> = [];
     this.list_product.forEach((product, index) => {
       const category: any = this.vhQueryCafe.getlocalCategory(product['id_category']);
       if (product.manysize) {
         product.subs.forEach(subProduct => {
           subProduct.units.forEach(unitsubProduct => {
             let row: Object = new Object();
             row[this.languageService.translate("_name")] = product['name'];
             row[this.languageService.translate("_subname")] = subProduct['name'];
             row[this.languageService.translate("_item_code")] = subProduct['item_code']
             row[this.languageService.translate("_category")] = category ? category['name'] : '';
             row[this.languageService.translate("_unit")] = unitsubProduct['unit'];
             row[this.languageService.translate("_brand")] = product['brand'];
             row[this.languageService.translate("_price_import")] = unitsubProduct['price_import'];
             row[this.languageService.translate("_price")] = unitsubProduct['price'];
             row[this.languageService.translate("_price2")] = unitsubProduct['price2'];
             row[this.languageService.translate("_barcode")] = unitsubProduct['barcode'];
             row[this.languageService.translate("_warning_number")] = subProduct['warning_number'];
             if (row) productExcel.push(row);
           });
         });
       }
       if (index == this.list_product.length - 1) {
         this.vhAlgorithm.exportXLSX(productExcel, this.languageService.translate("Product list have manysize"));
       }
     });
   }
 
 
 
   /**
    * xut danh sch l sn phm ra file excel v ti v
    * @example this.exportListOfProductLot()
    */
   exportListOfProductLot() {
     let productExcel: Array<any> = [];
     this.list_product.forEach((product, index) => {
       const category: any = this.vhQueryCafe.getlocalCategory(product['id_category']);
       if (!product.manysize) {
         if (product.manylot) {
           product.lots.forEach(lot => {
             let row: Object = new Object();
             row[this.languageService.translate("_name")] = product['name'];
             row[this.languageService.translate("_category")] = category ? category['name'] : '';
             row[this.languageService.translate("_lot_number")] = lot['lot_number'];
             row[this.languageService.translate("_date_exp")] = this.format_date(lot['date_exp']);
             row[this.languageService.translate("_date_mfg")] = this.format_date(lot['date_mfg']);
             row[this.languageService.translate("_lot_barcode")] = lot['barcode'];
             if (row) productExcel.push(row);
           });
         }
       } else {
         product.subs.forEach(subProduct => {
           if (subProduct.manylot) {
             subProduct.lots.forEach(lot => {
               let row: Object = new Object();
               row[this.languageService.translate("_name")] = product['name'] + ` (` + subProduct['name'] + `)`;
               row[this.languageService.translate("_category")] = category ? category['name'] : '';
               row[this.languageService.translate("_lot_number")] = lot['lot_number'];
               row[this.languageService.translate("_date_exp")] = this.format_date(lot['date_exp']);
               row[this.languageService.translate("_date_mfg")] = this.format_date(lot['date_mfg']);
               row[this.languageService.translate("_lot_barcode")] = lot['barcode'];
               if (row) productExcel.push(row);
             });
           }
         });
       }
       if (index == this.list_product.length - 1) {
         this.vhAlgorithm.exportXLSX(productExcel, this.languageService.translate("List of product batches"))
       }
     })
   }
 
 
 
   format_date(date) {
     return this.datePipe.transform(date, "dd/MM/yyyy HH:mm");
   }
 
 
 
   //** hm sort cho cc ct */
   name_col = false;          // chn sort ct tn sn phm
   unit_col = false;             // Chn sp xp theo n v
   barcode_col = false;          // Chn sp xp theo barcode
   name_category_col = false;     // Chn sp xp theo tn danh mc
   brand_col = false;            // Chn sp xp theo chi nhnh
   price_high_col = false;            // Chn sp xp theo gi
   sortTable(colName) {
     let methodName = colName === 'price_high' ? 'sortNumberby' : 'sortVietnameseby';
     let direction = this[colName + '_col'] ? 'ASC' : 'DESC';
 
     this.list_show_product = this.vhAlgorithm[methodName + direction]([...this.list_show_product], colName);
     this.list_show_product_not = this.vhAlgorithm[methodName + direction]([...this.list_show_product_not], colName);
     this.list_show_product_hidden = this.vhAlgorithm[methodName + direction]([...this.list_show_product_hidden], colName);
   }
 
 }
 