import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListComponent } from './list.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

import { AddProductComponent } from './add-product/add-product.component';
import { EditProductComponent } from './edit-product/edit-product.component';
import { CreateDataProductsComponent } from './create-data-products/create-data-products.component';
import { UnitsListComponent } from './units-list/units-list.component';
import { LotListComponent } from './lot-list/lot-list.component';
import { LotListUpdateComponent } from './lot-list-update/lot-list-update.component';
import { UnitsListUpdateComponent } from './units-list-update/units-list-update.component';
import { PrintFiveColComponent } from './edit-product/print-five-col/print-five-col.component';
import { PrintFourColComponent } from './edit-product/print-four-col/print-four-col.component';
import { PrintOneColComponent } from './edit-product/print-one-col/print-one-col.component';
import { DetailThreeColComponent } from './edit-product/detail-three-col/detail-three-col.component';
import { PrintFiveColLotComponent } from './edit-product/print-five-col-lot/print-five-col-lot.component';
import { PrintFourColLotComponent } from './edit-product/print-four-col-lot/print-four-col-lot.component';
import { PrintOneColLotComponent } from './edit-product/print-one-col-lot/print-one-col-lot.component';
import { PrintThreeColLotComponent } from './edit-product/print-three-col-lot/print-three-col-lot.component';
import { PrintTwoColLotComponent } from './edit-product/print-two-col-lot/print-two-col-lot.component';
import { DetailComponent } from './edit-product/detail/detail.component';
const routes: Routes = [
  {
    path: '',
    component:  ListComponent
  },
  {
    path: 'add',
    component:  AddProductComponent
  },
  {
    path: 'edit',
    component: EditProductComponent
  },
  {
    path: 'create-data',
    component: CreateDataProductsComponent
  },
  {
    path: 'print-two-col',
    component: DetailComponent
  },
  {
    path: 'print-three-col',
    component: DetailThreeColComponent
  },
  {
    path: 'print-one-col',
    component: PrintOneColComponent
  },
  {
    path: 'print-four-col',
    component: PrintFourColComponent
  },
  {
    path: 'print-five-col',
    component: PrintFiveColComponent
  },
  {
    path: 'print-two-col-lot',
    component: PrintTwoColLotComponent
  },
  {
    path: 'print-three-col-lot',
    component: PrintThreeColLotComponent
  },
  {
    path: 'print-one-col-lot',
    component: PrintOneColLotComponent
  },
  {
    path: 'print-four-col-lot',
    component: PrintFourColLotComponent
  },
  {
    path: 'print-five-col-lot',
    component: PrintFiveColLotComponent
  },
]

@NgModule({
  declarations: [
    ListComponent,
    AddProductComponent,
    EditProductComponent,
    CreateDataProductsComponent,UnitsListComponent,
    DetailComponent,
    LotListComponent,
    LotListUpdateComponent,
    UnitsListUpdateComponent,
    DetailThreeColComponent,
    PrintOneColComponent,
    PrintFourColComponent,
    PrintFiveColComponent,
    PrintTwoColLotComponent,
    PrintThreeColLotComponent,
    PrintOneColLotComponent,
    PrintFourColLotComponent,
    PrintFiveColLotComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    TranslateModule,
    IonicModule
  ]
})
export class ListModule { }
