import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VhAlgorithm, VhImage, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.scss'],
})
export class AddProductComponent implements OnInit {

  management_product_manysize: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .management_product_manysize;
  management_product_lot: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .management_product_lot;
  management_product_price2: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .management_product_price2;
  stateObj: any;
  productForm: FormGroup;
  price: any;
  price2: any;
  price_import: any;
  list_category: any;
  list_barcode: any;
  barcode: boolean = true;
  createProd: boolean = false;
  dataUnits: any[] = []; // danh sch n v quy i
  dataUnitMain = {};
  barcodes = [];



  constructor(
    public translate: TranslateService,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private vhImage: VhImage,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private router: Router,
    private cdf: ChangeDetectorRef
  ) {
    this.stateObj = this.router.getCurrentNavigation().extras.state.stateObj;
  }



  ngOnInit() {
    this.initForm();
    this.initData();
  }

  ngAfterViewChecked(): void {
    this.cdf.detectChanges();
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral('.price');
      this.price_import = this.vhAlgorithm.vhnumeral('.price_import');
      if (this.management_product_price2)
        this.price2 = this.vhAlgorithm.vhnumeral('.price2');
    });
  }

  /**
   * khi to form vi cc trng  lu sp
   * @param data nu c th gn d liu vo form
   * @example this.initForm()
   */
  initForm(data?) {
    this.productForm = new FormGroup({
      img: new FormControl(data ? data.img : ''),
      item_code : new FormControl(data ? data.item_code : ''),
      name: new FormControl(data ? data.name : '', [Validators.required]),
      barcode: new FormControl(''),
      unit: new FormControl('', [Validators.required]),
      price_import: new FormControl(
        0,
        Validators.compose([
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
          Validators.required,
        ])
      ),
      price: new FormControl(
        0,
        Validators.compose([
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
          Validators.required,
        ])
      ),
      price2: new FormControl(
        0,
        Validators.compose([
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
          Validators.required,
        ])
      ),
      id_category: new FormControl(data ? data.id_category : '', [
        Validators.required,
      ]),
      brand: new FormControl(data ? data.brand : ''),
      // allow_sell: new FormControl(true),
      warning_number: new FormControl(
        0,
        Validators.compose([
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
          Validators.required,
        ])
      ),
      selling: new FormControl(true),
      type: new FormControl(3),
      manysize: new FormControl(false),
      manylot: new FormControl(false),
      delete_hidden: new FormControl(false),
      tax: new FormControl(0),
    });
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/ingredient/list'], {
      state: { stateObj: this.stateObj },
    });
  }

  ngOnDestroy() {
    if (!this.createProd && this.productForm.value['img']) {
      this.vhImage.deleteThumbnail(this.productForm.value['img']);
    }
  }

  /**
   * thm sp v DB
   * @example this.saveProduct()
   */
  saveProduct() {
    this.productForm.removeControl('subProducts');
    let product = this.cleanForm(this.productForm).value;
    product.tax = 0;
    // nu c manysize th phi format li cc trng trong subs  lu
    if (product['manysize']) {
      delete product['item_code'];
      if (this.subProducts.length === 0) {
        this.vhComponent.showToast(
          1500,
          'Vui lng thng tin Add Size',
          'alert-toast'
        );
        return;
      } else {
        this.subProducts = this.subProducts.map((item) => {
          let sub_product = {
            name: item.name,
            warning_number: item.warning_number,
            item_code : item.item_code,
            // allow_sell: !item.delete_hidden,
            delete_hidden: false,
            selling: item.selling,
            units: [
              {
                unit: item.unit,
                ratio: 1,
                price: parseFloat(item.price),
                price2: this.management_product_price2
                  ? parseFloat(item.price2)
                  : 0,
                price_import: parseFloat(item.price_import),
                barcode: item.barcode,
                default: item.units.filter((e) => e.default).length
                  ? false
                  : true,
              },
              ...item.units,
            ],
            manylot: item.manylot,
          };
          if (item['manylot']) {
            sub_product['lots'] = item.lots;
            sub_product['days_import_warning'] = item.days_import_warning;
            sub_product['days_exp_warning'] = item.days_exp_warning;
          }
          return sub_product;
        });
      }
    } else {
      this.subProducts = [];
      product.units = new Array();
      product.price = parseFloat(
        this.price.getRawValue() ? this.price.getRawValue() : '0'
      );
      product.price_import = parseFloat(
        this.price_import.getRawValue() ? this.price_import.getRawValue() : '0'
      );
      product.price2 =
        this.management_product_price2 &&
        parseFloat(this.price2.getRawValue() ? this.price2.getRawValue() : '0');
      product.units = [...this.dataUnits];
      product.units = [
        {
          unit: product.unit,
          ratio: 1,
          price: parseFloat(product.price),
          price2: this.management_product_price2
            ? parseFloat(product.price2)
            : 0,
          price_import: parseFloat(product.price_import),
          barcode: product.barcode,
          default: product.units.filter((e) => e.default).length ? false : true,
        },
        ...product.units,
      ];
      if (product.warning_number >= 0)
        product.warning_number = parseFloat(product.warning_number);
      else product.warning_number = 0;
      if (product['manylot']) {
        product['lots'] = this.lot_list;
      }
    }
    Object.keys(product).forEach((key) => {
      if (
        key == 'unit' ||
        key == 'ratio' ||
        key == 'price' ||
        key == 'price2' ||
        key == 'price_import' ||
        key == 'barcode'
      )
        delete product[key];
    });
    if (!this.barcode) {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('Barcode already exists')
      );
    } else
      this.vhComponent.showLoading('').then(() => {
        this.vhQueryCafe
          .addProductAndSubsProduct_Cafe(product, this.subProducts)
          .then(
            () => {
              this.createProd = true;
              this.vhComponent.hideLoading(0);
              this.vhComponent.showToast(
                1000,
                product.name +
                  ' ' +
                  this.languageService.translate('has been added successfully'),
                'success-toast'
              );
              setTimeout(() => {
                this.goBack();
              }, 300);
            },
            (err) => {
              console.log(err);
              this.vhComponent.hideLoading(0);
              // this.vhComponent.alertMessageDesktop("error",err)
              this.vhComponent.showToast(
                1500,
                this.languageService.translate(
                  'An error occurred while adding product. Please try again'
                ),
                'alert-toast'
              );
            }
          );
      });
  }

  // Hm thc hin xa space (TRIM) cc control trong form
  public cleanForm(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((key) => {
      if (
        key === 'name' ||
        key === 'barcode' ||
        key === 'brand' ||
        key === 'unit'
      ) {
        if (formGroup.get(key).value) {
          let value = formGroup.get(key).value.toString().trim();
          formGroup.get(key).setValue(value);
        }
      }
    });
    return formGroup;
  }

  initData() {
    // ly danh mc
    this.list_category = this.vhQueryCafe.getlocalCategorys(3);
  }

  /**
   * kim tra barcode  tn ti hay cha
   * @example this.checkBarcode('123456')
   */
  checkBarcode(barcode: string) {
    let detailproduct =
      this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode);
    if (detailproduct == null) {
      if (
        this.dataUnits.find((e) => e.barcode == barcode) ||
        this.lot_list.find((lot) => lot.barcode == barcode)
      )
        this.barcode = false;
      else this.barcode = true;
    } else {
      this.barcode = false;
    }
  }

  /**
   * kim tra barcode ca sub  tn ti hay cha
   * @example this.checkBarcode('123456')
   */
  checkBarcodeSubProduct(barcode: string, sub_name) {
    let detailproduct =
      this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode);
    if (detailproduct == null) {
      if (this.barcodes.filter((item) => barcode && item == barcode).length)
        this.barcode = false;
      else this.barcode = true;
    } else {
      this.barcode = false;
    }
  }
  /**
   * m giao din chn file  upload nh
   * @example getFile()
   */
  getFile() {
    document.getElementById('file-upload').click();
  }

  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage
      .getThumbnailFromDesktop(
        file,
        256,
        256,
        'products',
        this.productForm.value['img'] || null
      )
      .then(
        (photoURL) => {
          this.productForm.controls['img'].setValue(photoURL);
          this.vhComponent.showToast(
            2000,
            this.languageService.translate('Image was loaded successfully'),
            'success-toast'
          );
        },
        () => {
          this.vhComponent.showToast(
            2000,
            this.languageService.translate('Image was failed'),
            'alert-toast'
          );
        }
      );
  }

  /**
   * to barcode t ng
   * @example this.changeBarcode()
   */
  changeBarcode() {
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10);
    }
    this.checkBarcode(newbarcode);
    if (this.barcode) {
      this.productForm.controls['barcode'].setValue(newbarcode);
    }
  }

  // CleaveJS
  tranformPrice(class_name) {
    switch (class_name) {
      case 'size-price-item':
        this.price_size = this.vhAlgorithm.vhnumeral('.size-price-item')
          ? this.vhAlgorithm.vhnumeral('.size-price-item')
          : 0;
        break;
      case 'size-price2-item':
        this.price2_size =
          this.management_product_price2 &&
          this.vhAlgorithm.vhnumeral('.size-price2-item')
            ? this.vhAlgorithm.vhnumeral('.size-price2-item')
            : 0;
        break;
      case 'size-price-purchase-item':
        this.price_size_purchase = this.vhAlgorithm.vhnumeral(
          '.size-price-purchase-item'
        )
          ? this.vhAlgorithm.vhnumeral('.size-price-purchase-item')
          : 0;
        break;
      case 'price':
        this.price = this.vhAlgorithm.vhnumeral('.price')
          ? this.vhAlgorithm.vhnumeral('.price')
          : 0;
        break;
      case 'price2':
        this.price2 =
          this.management_product_price2 &&
          this.vhAlgorithm.vhnumeral('.price2')
            ? this.vhAlgorithm.vhnumeral('.price2')
            : 0;
        break;
      case 'price_import':
        this.price_import = this.vhAlgorithm.vhnumeral('.price_import')
          ? this.vhAlgorithm.vhnumeral('.price_import')
          : 0;
        break;
    }
  }

  // Thm units
  showDrawerUnits = false;
  closeUnits(event) {
    if (typeof event != 'boolean') {
      if (!this.productForm.value.manysize) this.dataUnits = event;
      else {
        this.dataUnits = event
        this.formSubProduct.controls['units'].setValue(event);
      }
    }
    this.showDrawerUnits = false;
  }

  /**
   * m drawer danh sch n v
   * @example this.changeUnits()
   */
  changeUnits() {
    this.barcodes = [];
    if (!this.productForm.value.manysize) {
      this.barcodes = [this.productForm.value.barcode]
        .concat(
          this.dataUnits.map((item) => {
            return item.barcode;
          })
        )
        .concat(
          this.lot_list.map((lot) => {
            return lot.barcode;
          })
        );
    }
    this.showDrawerUnits = true;
    this.dataUnitMain = {
      unit: this.productForm.value.unit,
      ratio: 1,
      barcode: this.productForm.value.barcode,
      price: parseFloat(
        this.price.getRawValue() ? this.price.getRawValue() : '0'
      ),
      price2:
        this.management_product_price2 &&
        parseFloat(this.price2.getRawValue() ? this.price2.getRawValue() : '0'),
      price_import: parseFloat(
        this.price_import.getRawValue() ? this.price_import.getRawValue() : '0'
      ),
    };
  }

  /**
   * m drawer danh sch n v ca subproduct
   * @example this.changeUnitsSubs()
   */
  changeUnitsSubs() {
    this.barcodes = [];
    for (let i in this.subProducts) {
      this.barcodes = this.barcodes.concat([
        ...this.subProducts[i].units.map((e) => {
          return e.barcode;
        }),
        this.subProducts[i].barcode,
        ...this.subProducts[i].lots.map((lot) => {
          return lot.barcode;
        }),
      ]);
    }
    this.barcodes.push(
      this.formSubProduct.value.barcode,
      ...this.formSubProduct.value.units.map((item) => {
        return item.barcode;
      })
    );
    this.showDrawerUnits = true;
    this.dataUnits = this.formSubProduct.value.units;
    this.dataUnitMain = {
      unit: this.formSubProduct.value.unit,
      ratio: 1,
      barcode: this.formSubProduct.value.barcode,
      price: this.price_size ? parseFloat(this.price_size.getRawValue()) : 0,
      price2:
        this.management_product_price2 && this.price2_size
          ? parseFloat(this.price2_size.getRawValue())
          : 0,
      price_import: this.price_size_purchase
        ? parseFloat(this.price_size_purchase.getRawValue())
        : 0,
    };
  }

  //  cp nht trng form khi manysize: true/false
  statusCheckBox(e) {
    this.dataUnits = [];
    if (!e) {
      this.subProducts = [];
      this.initForm(this.productForm.value);
    } else {
      this.productForm.addControl(
        'subProducts',
        new FormControl('', [Validators.required])
      );
      this.productForm.removeControl('price');
      this.productForm.removeControl('price2');
      this.productForm.removeControl('price_import');
      this.productForm.removeControl('barcode');
      this.productForm.removeControl('warning_number');
      this.productForm.removeControl('unit');
      this.productForm.removeControl('selling');
      // this.productForm.removeControl('allow_sell');
      this.productForm.removeControl('manylot');
    }
  }


  /**
   * bt sk thay i thit lp l cho sp
   * @param event 
   * @example this.changeManyLot(true)
   */
  changeManyLot(event) {
    if (event) {
      this.productForm.addControl('days_import_warning', new FormControl(null));
      this.productForm.addControl('days_exp_warning', new FormControl(null));
    } else {
      this.productForm.removeControl('days_import_warning');
      this.productForm.removeControl('days_exp_warning');
    }
  }

  public subProducts = [];
  public isVisibleAddSize = false; // m model thm/ sa sub product

  /**Add Size */
  deleteSize(data) {
    this.subProducts = this.subProducts.filter((sub) => sub.name != data.name);
    if (this.subProducts.length == 0) {
      this.productForm.controls['subProducts'].setValue([]);
    } else {
      this.productForm.controls['subProducts'].setValue(this.subProducts);
    }
  }
  handleCancelAdd() {
    this.isVisibleAddSize = false;
  }

  price_size_purchase: any;
  price_size: any;
  price2_size: any;
  public formSubProduct: FormGroup;
  public titleSubProduct = '';
  public subAdd = true; // dng  xc nh dng form edit/ form add
  index_sub_update: number = 0;
  // hm thc hin thm hoc sa sub
  handleFormSubProduct(value) {
    if (!this.barcode) {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('Barcode already exists')
      );
    } else {
      let data = {
        ...value,
        name: value.name.toString().trim(),
        barcode: value.barcode.toString().trim(),
        unit: value.unit.toString().trim(),
        units: value.units,
        lots: value.lots,
        price: this.price_size.getRawValue()
          ? parseFloat(this.price_size.getRawValue())
          : 0,
        price2:
          this.management_product_price2 && this.price2_size.getRawValue()
            ? parseFloat(this.price2_size.getRawValue())
            : 0,
        price_import: this.price_size_purchase.getRawValue()
          ? parseFloat(this.price_size_purchase.getRawValue())
          : 0,
      };
      if (!this.subAdd) {
        this.subProducts[this.index_sub_update] = data;
      } else this.subProducts.push(data);
      this.productForm.controls['subProducts'].setValue(this.subProducts);
      this.isVisibleAddSize = false;
    }
  }

  // hin th Form thc hin add hoc l thc hin vic edit
  showSubProduct(data?) {
    this.isVisibleAddSize = true;
    this.barcodes = [];
    for (let i in this.subProducts) {
      this.barcodes = this.barcodes.concat([
        ...this.subProducts[i].units.map((e) => {
          return e.barcode;
        }),
        this.subProducts[i].barcode,
        ...this.subProducts[i].lots.map((lot) => {
          return lot.barcode;
        }),
      ]);
    }
    if (data) {
      this.titleSubProduct = this.languageService.translate(
        'Cp nht phn loi sn phm'
      );
      this.initFormSubProduct(data);
      this.subAdd = false;
      this.vhAlgorithm.waitingStack().then(() => {
        this.price_size = this.vhAlgorithm.vhnumeral('.size-price-item')
          ? this.vhAlgorithm.vhnumeral('.size-price-item')
          : 0;
        this.price2_size =
          this.management_product_price2 &&
          this.vhAlgorithm.vhnumeral('.size-price2-item')
            ? this.vhAlgorithm.vhnumeral('.size-price2-item')
            : 0;
        this.price_size_purchase = this.vhAlgorithm.vhnumeral(
          '.size-price-purchase-item'
        )
          ? this.vhAlgorithm.vhnumeral('.size-price-purchase-item')
          : 0;
      });
      this.index_sub_update = this.subProducts.findIndex(
        (item) => item.name == data.name
      );
    } else {
      this.titleSubProduct = this.languageService.translate(
        'Thm phn loi sn phm'
      );
      this.initFormSubProduct();
      this.subAdd = true;
    }
  }

  /**
   * 
   * @param data 
   * @example this.duplicateSubProduct(subproduct_selected)
   */
  duplicateSubProduct(data) {
    let value = { ...data };
    delete value.name;
    this.checkBarcodeSubProduct(value.barcode, '');
    this.initFormSubProduct(value);
    this.subAdd = true;
    this.titleSubProduct = this.languageService.translate(
      'Thm phn loi sn phm'
    );
    this.isVisibleAddSize = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.price_size = this.vhAlgorithm.vhnumeral('.size-price-item')
        ? this.vhAlgorithm.vhnumeral('.size-price-item')
        : 0;
      this.price2_size =
        this.management_product_price2 &&
        this.vhAlgorithm.vhnumeral('.size-price2-item')
          ? this.vhAlgorithm.vhnumeral('.size-price2-item')
          : 0;
      this.price_size_purchase = this.vhAlgorithm.vhnumeral(
        '.size-price-purchase-item'
      )
        ? this.vhAlgorithm.vhnumeral('.size-price-purchase-item')
        : 0;
    });
  }

  // Khi to form sub product
  initFormSubProduct(item?: any) {
    this.formSubProduct = new FormGroup({
      name: new FormControl(item ? item['name'] : '', [Validators.required]),
      price_import: new FormControl(
        item ? item['price_import'] : '',
        Validators.compose([
          Validators.required,
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
        ])
      ),
      price: new FormControl(
        item ? item['price'] : '',
        Validators.compose([
          Validators.required,
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
        ])
      ),
      price2: new FormControl(
        item ? item['price2'] : '',
        Validators.compose([
          Validators.pattern(
            '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
          ),
        ])
      ),
      warning_number: new FormControl(item ? item['warning_number'] : 0, [
        Validators.required,
      ]),
      barcode: new FormControl(item ? item['barcode'] : ''),
      unit: new FormControl(item ? item['unit'] : ''),
      units: new FormControl(item ? item['units'] : []),
      // allow_sell: new FormControl(item ? item['allow_sell'] : true),
      delete_hidden: new FormControl(item ? item['delete_hidden'] : false),
      selling: new FormControl(item ? item['selling'] : true),
      manylot: new FormControl(item ? item['manylot'] : false),
      days_import_warning: new FormControl(
        item ? item['days_import_warning'] : ''
      ),
      days_exp_warning: new FormControl(item ? item['days_exp_warning'] : ''),
      lots: new FormControl(item ? item['lots'] : []),
      item_code: new FormControl(item ? item['item_code'] : ''),
    });
  }

  /**
   * to barcode t ng cho subproduct
   * @example this.changeBarcodeSubProduct()
   */
  changeBarcodeSubProduct() {
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10);
    }
    this.checkBarcode(newbarcode);
    if (this.barcode) {
      this.formSubProduct.controls['barcode'].setValue(newbarcode);
    }
  }

  show_lot_list = false;
  lot_list: any = [];

  /**
   * m drawer danh sch l sp
   * @param lots 
   * @example this.openListLot([])
   */
  openListLot(lots?) {
    this.barcodes = [];
    if (!this.productForm.value.manysize) {
      console.log([this.productForm.value.barcode]);

      this.barcodes = [this.productForm.value.barcode].concat(
        this.dataUnits.map((item) => {
          return item.barcode;
        })
      );
    } else {
      for (let i in this.subProducts) {
        this.barcodes = this.barcodes.concat([
          ...this.subProducts[i].units.map((e) => {
            return e.barcode;
          }),
          this.subProducts[i].barcode,
          ...this.subProducts[i].lots.map((lot) => {
            return lot.barcode;
          }),
        ]);
      }
      this.barcodes.push(
        this.formSubProduct.value.barcode,
        ...this.formSubProduct.value.units.map((item) => {
          return item.barcode;
        })
      );
    }
    console.log(this.barcodes);

    this.show_lot_list = true;
    if (lots) this.lot_list = lots;
  }

  /**
   * bt sk nhn nt lu drawer l sp
   * @param lot_list 
   */
  handleLotList(lot_list) {
    this.show_lot_list = false;
    if (lot_list) {
      if (this.productForm.value.manysize) {
        this.formSubProduct.controls['lots'].setValue(lot_list);
      } else this.lot_list = lot_list;
    }
    console.log(lot_list);
  }
  /**
   * bt sk thay i thit lp l cho subproduct
   * @param event 
   * @example thsi.changeManyLotSub(true)
   */
  changeManyLotSub(event) {
    if (event) {
      this.formSubProduct.addControl(
        'days_import_warning',
        new FormControl(null)
      );
      this.formSubProduct.addControl('days_exp_warning', new FormControl(null));
    } else {
      this.formSubProduct.removeControl('days_import_warning');
      this.formSubProduct.removeControl('days_exp_warning');
    }
  }

  gotoConfigProduct() {
    this.router.navigate(['/cafe/dashboard/settings/configuration/products']);
  }
    
    
    
  /** Nu l true th l ang thm danh mc, dng  hin th trng thi loading, trnh b nhn nhiu ln */
  isAddingCategory: boolean = false;
  /** Input nhp tn danh mc */
  @ViewChild('categoryName') categoryName;

  /**
   * Thm danh mc
   */
  addCategory(categoryName: any) {
    const value = categoryName.value ? categoryName.value?.toString()?.trim() : '';
    if (value) {
      // Kim tra xem danh mc  tn ti hay cha
      let index = this.vhQueryCafe.getlocalCategorys()
                    .filter(res => res.type == VhType.INGREDIENT)
                    .findIndex(item => this.vhAlgorithm.changeAlias(item.name).toLowerCase() == this.vhAlgorithm.changeAlias(value).toLowerCase());

      //  tn ti
      if (index !== -1) {
        this.vhComponent.showToast(1500, this.languageService.translate("Category existed"), 'alert-toast');
        return;
      }

      // Cha tn ti th thm
      this.isAddingCategory = true;
      this.vhQueryCafe.addCategory({ name: value, type: VhType.INGREDIENT }).then((res: any) => {
        this.categoryName.nativeElement.value = ''; // Reset input
        this.list_category.unshift(res); // Thm vo u danh sch
        this.productForm.controls['id_category'].setValue(res._id); // Chn ci mi thm
        this.vhComponent.showToast(2000, (this.languageService.translate("Category has been added successfully")), 'success-toast');
      }, err => {
        console.log(err);
        this.vhComponent.showToast(1500, this.languageService.translate("An eror occured while adding category. Please try again"), 'alert-toast');
      }).finally(() => {
        this.isAddingCategory = false;
      });
    }
    else {
      this.vhComponent.showToast(1500, this.languageService.translate('Category name is required'), 'alert-toast');
    }
  }

}
