import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks'; 
import { VhComponent } from '../../../../../../components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
@Component({
  selector: 'app-lot-list-update',
  templateUrl: './lot-list-update.component.html',
  styleUrls: ['./lot-list-update.component.scss']
})
export class LotListUpdateComponent implements OnInit {
   @Input() id_sub_or_product: string = '';
    @Input() show_lot_list: boolean = false;
    data: any[];
    @Output("close") close = new EventEmitter<any>();
    public formLots = this.formBuilderfb.group({                       // Form thm n v quy i
      lots: this.formBuilderfb.array([], Validators.required)
    })
    public lotForm: FormGroup;
    formAddLot: FormGroup;
    show_modal_add_lot = false;
    is_change_to_update = false
    constructor(
      public vhAlgorithm: VhAlgorithm,
      private readonly formBuilderfb: FormBuilder,
      private vVhQueryCafe: VhQueryCafe,
      private vhComponent: VhComponent,
      private lang: LanguageService,
      private functionService: FunctionService
    ) {
  
    }
    ngOnInit(): void {
      this.data = this.vVhQueryCafe.getlocalDetailProduct(this.id_sub_or_product).lots;
      this.initAddLotsForm(this.data)
    }
  
    initAddLotsForm(data) {
      if (data.length > 0) {
        for (let i = 0; i < data.length; i++) { //  n v quy i
          this.addLots(data[i])
        }
      }
      
  
    }
  
    /**
     * gn d liu l vo form  d x l
     */
    addNewLots() {
      this.show_modal_add_lot = true;
      this.formAddLot = new FormGroup({
        date_mfg: new FormControl(new Date(new Date().setHours(0, 0, 0,)), Validators.compose([Validators.required])),
        date_exp: new FormControl(new Date(new Date().setHours(23, 59, 59,)), Validators.compose([Validators.required])),
        lot_number: new FormControl('', Validators.compose([Validators.required])),
        hidden: new FormControl(false),
        barcode: new FormControl(''),
      })
    }
  
    /**
     * bt sk nhn nt ok modal thm l
     * @example this.handleOkNewLot()
     */
    handleOkNewLot() {
      if (!this.exist_barcode_add) {
        
        
        this.vVhQueryCafe.addLotProduct_Cafe(this.id_sub_or_product, this.formAddLot.value)
          .then((lot) => {
            
            this.addLots({ ...this.formAddLot.value, _id: lot })
            this.show_modal_add_lot = false;
            this.is_change_to_update = true
          }, error => {
            
          })
      }
      else this.vhComponent.alertMessageDesktop("error", this.lang.translate("Barcode ny  tn ti"))
    }
  
    // get mng l quy i
    get lots() {
      return this.formLots.controls["lots"] as FormArray;
    }
    // thm l quy i
    addLots(lot?) {
      this.lotForm = this.formBuilderfb.group({
        date_mfg: new FormControl(lot ? new Date(lot.date_mfg) : new Date(new Date().setHours(0, 0, 0,)), Validators.compose([Validators.required])),
        date_exp: new FormControl(lot ? new Date(lot.date_exp) : new Date(new Date().setHours(23, 59, 59,)), Validators.compose([Validators.required])),
        lot_number: new FormControl(lot ? lot.lot_number : '', Validators.compose([Validators.required])),
        hidden: new FormControl(lot ? lot.hidden : false),
        barcode: new FormControl(lot ? lot.barcode : ''),
        _id: new FormControl(lot ? lot._id : null),
      })
      this.lots.push(this.lotForm);
    }
  
    /**
     * cp nht m l ln DB
     * @param id 
     * @param value 
     */
    updateLotNumber(id, value) {
      if (this.exist_lot_number && this.checkLotNumber(value)) {
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Lot number already exists"))
      } else {
        this.updateLotProduct(id, { lot_number: value })
      }
    }
  
    /**
     * cp nht barcode l ln DB
     * @param id 
     * @param value 
     */
    updateBarcode(id, value) {
      if (this.barcode_exist) {
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Barcode already exists"))
      } else {
        this.updateLotProduct(id, { barcode: value })
      }
    }
  
    /**
     * cp nht cc trng khc ca l ln DB
     * @param id 
     * @param value 
     */
    updateLotProduct(id, value) {
      this.vVhQueryCafe.updateLotProduct_Cafe(this.id_sub_or_product, id, value)
        .then((bool) => {
          
          if (!bool) this.vhComponent.alertMessageDesktop("error", this.lang.translate("Cp nht tht bi"))
        }, error => {  })
    }
    // xa n v quy i
    deleteLots(index: number, id_lotproduct) {
  
      this.vVhQueryCafe.deleteLotProduct_Cafe(this.id_sub_or_product, id_lotproduct)
        .then((rsp: any) => {
          
  
          if (rsp.vcode === 0) {
            this.lots.removeAt(index)
            this.is_change_to_update = true
          } else if (rsp.vcode === 1) {
            this.vhComponent.alertMessageDesktop("error", this.lang.translate("Bn khng th xa, v l sn phm ny khng tn ti"))
          } else if (rsp.vcode === 2) {
            this.vhComponent.alertMessageDesktop("error", this.lang.translate("Bn khng th xa, v l sn phm ny  pht sinh ha n"))
          }
        }, error => {
          
  
        })
    }
    /**
     * kim tra ngy sx v ngy hh hp l
     * @param formAddLot 
     */
    changeDate(formAddLot) {
      this.is_change_to_update = true
      if (new Date(formAddLot.value.date_mfg).getTime() >= new Date(formAddLot.value.date_exp).getTime()) {
        formAddLot.control['date_exp'].setValue(new Date(new Date(formAddLot.value.date_mfg).setHours(23, 59, 59)))
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Staring date must be less than or the same as ending date"))
      }
      else {
        this.updateLotProduct(formAddLot.value._id, { date_exp: formAddLot.value.date_exp, date_mfg: formAddLot.value.date_mfg })
      }
    }
  
    /**
     * ng component
     */
    okLots() {
      // 
      // 
      if (this.barcode_exist) {
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Barcode already exists"))
      }
      else {
        if (this.is_change_to_update) {
          let invalid_lot = this.lots.value.filter(item => new Date(item.date_exp).getTime() <= new Date(new Date(item.date_mfg)).getTime())
          if (invalid_lot.length) {
            this.vhComponent.alertMessageDesktop("error", invalid_lot[0].lot_number + ' ' + this.lang.translate("Staring date must be less than or the same as ending date"))
          }
          else {
            let data = this.lots.value.filter(item => item.lot_number != '' && new Date(item.date_exp).getTime() >= new Date(new Date(item.date_mfg)).getTime())
            
  
            this.close.emit(data);
          }
        }
        else this.close.emit(null);
      }
    }
  
    nzclose() {
      this.okLots()
    }
  
    /**
     * to barcode t ng
     * @returns string
     * @example let barcode = this.createBarcodeAuto()
     */
    createBarcodeAuto() {
      let newbarcode = '';
      for (let index = 0; index < 12; index++) {
        newbarcode += Math.floor(Math.random() * 10)
      }
      let detailproduct = this.vVhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(newbarcode)
      if (detailproduct == null) {
        return newbarcode
      }
    }
  
    /**
     * to barcode t ng cho l  thm
     * @param position 
     */
    setBarcodeForUnit(position) {
      (this.lots.at(position) as FormGroup).get('barcode').setValue(this.createBarcodeAuto());
      document.getElementById('barcodeError' + position).style.display = 'none';
      this.is_change_to_update = true
      this.updateBarcode(this.lotForm.value._id, this.lotForm.value.barcode)
    }
  
  
  
    /**
     * to barcode t ng cho l thm mi
     * @param position 
     */
    setBarcodeForNewLot() {
      this.formAddLot.controls['barcode'].setValue(this.createBarcodeAuto());
    }
  
    barcode_exist = false;
    /**
     * kim tra barcode  tn ti hay cha
     * @param barcode 
     * @param position v tr ca l trong mng  lm css nu trng
     * @example this.checkBarcode('123456789',1) 
     */
    checkBarcode(barcode, position) {
      this.is_change_to_update = true
      if (barcode.length) {
        if (this.vVhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode)) {
          this.barcode_exist = true;
          document.getElementById('barcodeError' + position).style.display = 'block';
          return;
        }
        else {
          const lotsNotPosition: any[] = this.lots.value.filter((item, index) => index != position)
          if (lotsNotPosition.length) {
            lotsNotPosition.forEach(element => {
              if (element.barcode != barcode) {
                this.barcode_exist = false;
                document.getElementById('barcodeError' + position).style.display = 'none';
                return
              } else {
                this.barcode_exist = true;
                document.getElementById('barcodeError' + position).style.display = 'block';
                return;
              }
            })
          } else {
            this.barcode_exist = false;
            document.getElementById('barcodeError' + position).style.display = 'none';
            return
          }
        }
      } else {
        this.barcode_exist = false;
        document.getElementById('barcodeError' + position).style.display = 'none';
        return
      }
    }
  
  
    lot_number_exist = false;
    //  kim tra n v khi thm
    checkNameUnit(lot: string, position: number) {
      this.is_change_to_update = true
      if (lot.length) {
        const lotsNotPosition: any[] = this.lots.value.filter((item, index) => index != position)
        if (lotsNotPosition.length) {
          lotsNotPosition.forEach(element => {
            if (element.lot_number != lot) {
              document.getElementById('lotError' + position).style.display = 'none';
              this.lot_number_exist = false;
              return
            } else {
              document.getElementById('lotError' + position).style.display = 'block';
              this.lot_number_exist = true;
              return;
            }
          })
        } else {
          document.getElementById('lotError' + position).style.display = 'none';
          this.lot_number_exist = false;
          return
        }
      }
    }
  
    exist_lot_number = false;
    checkLotNumber(lot) {
      this.exist_lot_number = this.functionService.checkExitsLotNumber(lot)
      return this.functionService.checkExitsLotNumber(lot)
    }
    exist_barcode_add = false;
    checkBarcodeAdd(barcode) {
      if (this.vVhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode)) {
        this.exist_barcode_add = true;
      }
      else this.exist_barcode_add = false;
    }
  }
  