import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import jsbarcode from 'jsbarcode';
import printJS from 'print-js';
import { Platform } from '@ionic/angular';
declare var electron: any;
@Component({
  selector: 'app-print-one-col-lot',
  templateUrl: './print-one-col-lot.component.html',
  styleUrls: ['./print-one-col-lot.component.scss']
})
export class PrintOneColLotComponent implements OnInit {

  stamp: any;
  listOfFontSize = [8, 9, 10, 11, 12, 13, 14, 15, 16];
  number = 1;
  listRowBarcode = [false]; // cha s hng hin th. true th hin th 2 tem. false l hien th 1 tem trn 1 hng
  heightScroll: any;
  propertySelected: any;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private cdf: ChangeDetectorRef,
    private vhComponent: VhComponent,
    private vhQuery: VhQuery,
    public platform: Platform
  ) {
    this.stamp = this.router.getCurrentNavigation().extras.state;
    this.stamp.barcode.property = this.vhAlgorithm.sortNumberbyASC(this.stamp.barcode.property, 'y')
  }

  ngOnInit(): void {
    this.changeNummber()
    for (let item of this.stamp.barcode.property) {
      switch (item.type) {
        case 'date_exp': item.value = this.stamp.formProduct.date_exp; break;
        case 'date_mfg': item.value = this.stamp.formProduct.date_mfg; break;
        case 'lot_number': item.value = this.stamp.formProduct.lot_number; break;
        case 'barcode': item.value = this.stamp.formProduct.barcode; break;
        case 'name': item.value = this.stamp.formProduct.name; break;
      }
    }
    console.log(this.stamp.barcode.property);
    
    this.propertySelected = this.stamp.barcode.property[0];

  }

  changeNummber() {
    this.listRowBarcode = []
    for (let i = 0; i < this.stamp.number; i++) {
      this.listRowBarcode.push(true);
    }
  }

  ngAfterViewInit(): void {
    if (document.querySelector(".barcode"))
      this.renderBarcode();
  }

  ngAfterViewChecked(): void {

    if (document.querySelector('#scroll')) this.heightScroll = document.querySelector('#scroll').clientHeight - 32;
    this.cdf.detectChanges();
  }

  renderBarcode() {
    if (this.stamp.formProduct.barcode)
      jsbarcode(".barcode", this.stamp.formProduct.barcode, {
        displayValue: this.stamp.barcode.property[1].hidden_text,
        fontSize: this.stamp.barcode.property[1].font_size,
        font: "sans-serif"
      });
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/ingredient/list/edit'], { state: this.stamp })
  }
  Print() {
    if (this.platform.is("electron")) this.printElectron()
    else this.printBrowser()
  }
  printElectron() {
    let html = document.getElementById("stamp-barcode-electron").innerHTML;
    electron.ipcRenderer.send("print-start", {
      html: html,
      deviceName: this.stamp.printer_name,
    });
    electron.ipcRenderer.on("print-done", (event, data) => {
      if (!data) this.vhComponent.alertMessageDesktop('warning', 'Printing failed', 3000)

    });
  }
  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height - 4) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }

}
