import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-units-list-update',
  templateUrl: './units-list-update.component.html',
  styleUrls: ['./units-list-update.component.scss']
})
export class UnitsListUpdateComponent implements OnInit {
  unit_main: any;
  @Input("showDrawerUnits") showDrawerUnits: boolean = false;
  @Input("dataUnit") dataUnit: any
  @Input("dataUnits") dataUnits: any[];
  @Output("closeUnits") closeUnits = new EventEmitter<any>();
  ratio_default: number = 1;
  public formUnits = this.formBuilderfb.group({                       // Form thm n v quy i
    units: this.formBuilderfb.array([], Validators.required)
  })
  public unitForm: FormGroup;
  ratioError = true;
  is_change_to_update = false;

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private formBuilderfb: FormBuilder,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private languageService: LanguageService
  ) {
  }
  ngOnInit(): void {
    this.ratio_default = this.dataUnits.find(e => e.default) ? this.dataUnits.find(e => e.default).ratio : 1
    this.initAddUnitsForm(this.dataUnits)
  }
  ngAfterViewInit() {
    for (let i = 0; i < this.units.value.length; i++) { //  n v quy i
      if (this.units.value[i].ratio != 1) {
        this.vhAlgorithm.vhnumeral("#" + 'price_units' + i)
        this.vhAlgorithm.vhnumeral("#" + 'price2_units' + i)
        this.vhAlgorithm.vhnumeral("#" + 'priceImport_units' + i)
      }

    }
  }

  initAddUnitsForm(data) {
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) { //  n v quy i
        if (data[i].ratio != 1) this.addUnits(data[i])
        if (data[i].ratio == 1) this.unit_main = data[i]
      }
    }
  }

  // get mng n v quy i
  get units() {
    return this.formUnits.controls["units"] as FormArray;
  }
  // thm n v quy i
  addUnits(unit?) {
    this.unitForm = this.formBuilderfb.group({
      unit: new FormControl(unit ? unit.unit : '', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      price: new FormControl(unit ? unit.price : '', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      price2: new FormControl(unit ? unit.price2 : '', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      price_import: new FormControl(unit ? unit.price_import : '', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      barcode: new FormControl(unit ? unit.barcode : ''),
      ratio: new FormControl(unit ? unit.ratio : '', [Validators.required]),
      default: new FormControl(unit ? unit.default : false, [Validators.required]),
    })
    this.units.push(this.unitForm);
  }
  // xa n v quy i
  deleteUnits(index: number) {
    this.units.removeAt(index)
    this.is_change_to_update = true;
    this.unit_main.default = true;
    this.barcode_exist = false;
    this.ratio_default = 1
  }


  /**
   * bt sk nhn nt lu
   */
  okUnits() {
    if (this.barcode_exist) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Barcode already exists"))
    }
    else {
      if (this.is_change_to_update) {
        this.dataUnits = this.units.value.filter(item => item.unit != '' &&  typeof(item.price) == 'number'  && typeof(item.price2)  == 'number' && typeof(item.price_import)  == 'number' && item.ratio )
        if (this.unit_main) this.dataUnits.unshift(this.unit_main)
        this.dataUnits.forEach((unit) => {
          if (unit.ratio == this.ratio_default) unit.default = true;
          else unit.default = false;
        });
        setTimeout(() => {
          this.closeUnits.emit(this.dataUnits);
        }, 100);

      }
      else this.closeUnits.emit(null);
    }
  }

  /**
   * ng component
   */
  close(success?) {
    this.okUnits()
  }

  changeDefault() {
    this.is_change_to_update = true
  }
  /**
   * to barcode t ng ti v tr position trong mng n v
   * @example setBarcodeForUnit(1)
   */
  setBarcodeForUnit(position) {
    this.is_change_to_update = true
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10)
    }
    let detailproduct = this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(newbarcode)
    if (detailproduct == null) {
      (this.units.at(position) as FormGroup).get('barcode').setValue(newbarcode);
      document.getElementById('barcodeError' + position).style.display = 'none';
    } else {
      document.getElementById('barcodeError' + position).style.display = 'block';
    }
  }

  /**
   * bt sk thay i gi bn
   * @param nameClass 
   * @param position 
   */
  changeUnitsPrice(nameClass, position) {
    this.is_change_to_update = true
    let price = this.vhAlgorithm.vhnumeral("#" + nameClass);
    (this.units.at(position) as FormGroup).get('price').patchValue(parseFloat(price.getRawValue() ? price.getRawValue() : '0'))
    this.vhAlgorithm.vhnumeral("#" + nameClass);
  }

  /**
   * bt sk thay i gi bn 2
   * @param nameClass 
   * @param position 
   */
  changeUnitsPrice2(nameClass, position) {
    this.is_change_to_update = true
    let price = this.vhAlgorithm.vhnumeral("#" + nameClass);
    (this.units.at(position) as FormGroup).get('price2').patchValue(parseFloat(price.getRawValue() ? price.getRawValue() : '0'))
    this.vhAlgorithm.vhnumeral("#" + nameClass);
  }

  /**
   * bt sk thay i gi nhp
   * @param nameClass 
   * @param position 
   */
  changeUnitsPriceImport(nameClass, position) {
    this.is_change_to_update = true
    let price = this.vhAlgorithm.vhnumeral("#" + nameClass);
    (this.units.at(position) as FormGroup).get('price_import').patchValue(parseFloat(price.getRawValue() ? price.getRawValue() : '0'))
    this.vhAlgorithm.vhnumeral("#" + nameClass);
  }

  /**
   * bt sk thay i gi tr ratio ca n v
   * @param ratio 
   * @param position 
   */
  changeUnitsRatio(ratio, position) {
    this.is_change_to_update = true
    let value = parseFloat(ratio);
    // Thc hin lc d liu units khc vi v tr nhp hin ti
    // V gi tr nhp vo phi khc vi bt c gi tr Ratio no trong mng va lc
    //  Tn ti mng c chiu di th gi tr   tn ti
    if (value == 1) {
      document.getElementById('ratioError' + position).style.display = 'block';
    } else {
      const unitsDef: any[] = this.units.value.filter((item, index) => index != position);
      if (unitsDef.length) {
        if (unitsDef.find(item => item.ratio == value)) {
          document.getElementById('ratioError' + position).style.display = 'block';
        } else {
          (this.units.at(position) as FormGroup).get('ratio').setValue((value));
          document.getElementById('ratioError' + position).style.display = 'none';
        }
      } else {
        document.getElementById('ratioError' + position).style.display = 'none';

      }
    }
  }

  barcode_exist = false;
  /**
   * kim tra barcode  tn ti hay cha
   * @param barcode 
   * @param position 
   *  
   */
  checkBarcode(barcode, position) {
    this.is_change_to_update = true
    if (barcode.length) {
      if (this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode)) {
        this.barcode_exist = true;
        document.getElementById('barcodeError' + position).style.display = 'block';
        return;
      }
      else {
        const unitsNotPosition: any[] = this.units.value.filter((item, index) => index != position)
        if (unitsNotPosition.length) {
          unitsNotPosition.forEach(element => {
            if (element.barcode != barcode) {
              this.barcode_exist = false;
              document.getElementById('barcodeError' + position).style.display = 'none';
              return
            } else {
              this.barcode_exist = true;
              document.getElementById('barcodeError' + position).style.display = 'block';
              return;
            }
          })
        } else {
          this.barcode_exist = false;
          document.getElementById('barcodeError' + position).style.display = 'none';
          return
        }
      }
    } else {
      this.barcode_exist = false;
      document.getElementById('barcodeError' + position).style.display = 'none';
      return
    }
  }



  //  kim tra n v khi thm
  checkNameUnit(unit: string, position: number) {
    this.is_change_to_update = true
    if (unit.length) {
      if (unit == this.dataUnit) {
        document.getElementById('unitError' + position).style.display = 'block';
        return;
      } else {
        const unitsNotPosition: any[] = this.units.value.filter((item, index) => index != position)
        if (unitsNotPosition.length) {
          unitsNotPosition.forEach(element => {
            if (element.unit != unit) {
              document.getElementById('unitError' + position).style.display = 'none';
              return
            } else {
              document.getElementById('unitError' + position).style.display = 'block';
              return;
            }
          })
        } else {
          document.getElementById('unitError' + position).style.display = 'none';
          return
        }
      }
    }
  }
}
